
  import Msg from 'vux/src/components/msg/index.vue'

  export default{
      data(){
          return{
              data:{},
            route_type:0,
            atype:"",
            type:""  //1 2
          }
      },
      mounted(){
        let route_type = parseInt(this.$route.query.route_type);
        document.getElementById("index_loading").style.display="none";
        console.log(route_type);
        let atype = parseInt(this.$route.query.atype);
        //
        switch(route_type){
          case 1:
            this.data={
              desc:"",
              this_title:"",
              icon:"",
              buttons: [{
                type: 'primary',
                text: '',
                onClick: this.goIndex.bind(this)
              },{
                type: 'default',
                text: '',
                onClick: this.goList.bind(this)
              }]
            };
            break;
          //
          case 2:
            if(atype===0){
              this.data={
                desc:"",
                this_title:"",
                icon:"",
                buttons: [{
                  type: 'primary',
                  text: '',
                  onClick: this.go_smkm.bind(this)
                },{
                  type: 'default',
                  text: '',
                  onClick: this.go_fkyq.bind(this)
                }, {
                  type: 'default',
                  text: '',
                  onClick: this.go_ysgl.bind(this)
                }]
              };
            }
            break;
          case 3:
            this.data={
              desc:"",
              this_title:"",
              icon:"warn",
            };
            break;
          case 4:
            this.data={
              desc:"",
              this_title:"",
              icon:"warn",
            };
            break;
            //
          case 5:
            this.data={
              desc:"",
              this_title:"",
              icon:"",
              buttons: [{
                type: 'primary',
                text: '',
                onClick: this.goIndex.bind(this)
              },{
                type: 'default',
                text: '',
                onClick: this.goList.bind(this)
              }]
            };
            break;
          case 6:
            this.data={
              desc:"",
              this_title:"",
              icon:"",
              buttons: [{
                type: 'primary',
                text: '',
                onClick: this.goIndex.bind(this)
              },{
                type: 'default',
                text: '',
                onClick: this.goList.bind(this)
              }]
            };
            break;
          case 7:
            this.data={
              desc:"",
              this_title:"",
              icon:"",
              buttons: [{
                type: 'primary',
                text: '',
                onClick: this.goIndex.bind(this)
              },{
                type: 'default',
                text: '',
                onClick: this.goList.bind(this)
              }]
            };
            break;
          case 8:
            this.data={
              desc:"",
              this_title:"",
              icon:"",
              buttons: [{
                type: 'primary',
                text: '',
                onClick: this.goIndex.bind(this)
              },{
                type: 'default',
                text: '',
                onClick: this.goList.bind(this)
              }]
            };
            break;
          case 9:
            this.type = parseInt(this.$route.query.type);
            let type_name = this.type===1?"":"";
            this.data={
              desc:"",
              this_title:"",
              icon:"",
              buttons: [{
                type: 'primary',
                text: type_name+'',
                onClick: this.go_wb_Index.bind(this)
              },{
                type: 'default',
                text: '',
                onClick: this.go_wb_list.bind(this)
              }]
            };
            break;
          case 10:
            this.type = parseInt(this.$route.query.type);
            let type_name2 = this.type===1?"":"";
            this.data={
              desc:"",
              this_title:"",
              icon:"",
              buttons: [{
                type: 'primary',
                text: type_name2+'',
                onClick: this.go_wb_Index.bind(this)
              },{
                type: 'default',
                text: '',
                onClick: this.go_wb_list.bind(this)
              }]
            };
            break;
          case 11:
            this.type = parseInt(this.$route.query.type);
            let type_name3 = this.type===1?"":"";
            this.data={
              desc:"",
              this_title:"",
              icon:"",
              buttons: [{
                type: 'primary',
                text: type_name3+'',
                onClick: this.go_wb_Index.bind(this)
              },{
                type: 'default',
                text: '',
                onClick: this.go_wb_list.bind(this)
              }]
            };
            break;
          case 12:
            this.data={
              desc:"",
              this_title:"",
              icon:"",
              buttons: [{
                type: 'primary',
                text: '',
                onClick: this.go_tzsc_index.bind(this)
              },{
                type: 'default',
                text: '',
                onClick: this.go_tzsc_list.bind(this)
              }]
            };
            break;
        }
      },
      components:{
        Msg
      },
      methods:{
        goIndex(){
          this.$router.push({path: 'area_index'});
        },
        goList(){
          this.$router.push({path: '/common_order/0'});
        },
        go_smkm(){
          this.$router.push({path: 'my_code',query:{route_type:0}});
        },
        go_fkyq(){
          this.$router.push({path: 'invite_guest',query:{route_type:0}});
        },
        go_ysgl(){
          this.$router.push({path: 'key_manage',query:{route_type:0}});
        },
        go_wb_Index(){
            if(this.type===1){
              this.$router.push({path: 'wz_bj',query:{route_type:1}});
            }else{
              this.$router.push({path: 'wz_bj',query:{route_type:2}});
            }
//          this.$router.push({path: 'wz_bj'});
        },
        go_wb_list(){
            if(this.type===1){
              this.$router.push({path: '/common_order/2'});
            }else{
              this.$router.push({path: '/common_order/1'});
            }
        },
        go_tzsc_index(){
          this.$router.push({path: 'market_index'});
        },
        go_tzsc_list(){
          this.$router.push({path: 'my_published'});
        }
      }
  }



